.page__main-background {
  display: block;
  position: relative;
  height: 389px;
  width: 320px;
  overflow: hidden;
  margin: 0 auto;
  z-index: 0;
  // background-image: url("/img/background.png");
  background-size: cover;
  background-position: center;

  @media (min-width: @tablet-width) {
    width: 768px;
    height: 518px;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
  }
}

.page__main-background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
